<template>

  <div class="imagesOnServer">
    <h1>Images On Server by Marouane Ouriche</h1>

    <h3>Individual Images :</h3>
    <select v-model="selected">
      <option v-for="img in response" v-bind:key="img.name" v-bind:value="img.id" @click="getImg(img.id, 'myImg')">
        {{ img.name }}
      </option>
    </select><br/>

    <button id="hide" @click="hideImg('myImg')">Hide/Show image</button><br/>
    <button id="download" @click="downloadImage(selected)">Download image locally</button><br/>
    <button id="delete" @click="deleteImage(selected)">Delete image</button>

    <img id="myImg">

    <h3>Image Upload :</h3>
    <div class="container">
      <div>
        <label>File
          <input type="file" id="file" ref="file" v-on:change="handleFileUpload()"/>
        </label>
          <button v-on:click="submitFile()">Submit</button>
      </div>
    </div><br/>

    <h3>Images Gallery :</h3>
    <button @click="LoadGallery()">Hide/Show as gallery</button> <br/><br/>

    <div class="parent">
      <div class="gallery" v-for="img in response" v-bind:key="img.name">
          <img :id="img.name.substr(0, img.name.lastIndexOf('.'))">
      </div>
    </div>

    <template v-for="img in response"> {{ showImg(img.id, img.name.substr(0, img.name.lastIndexOf('.'))) }} </template>
    
  
  </div>

</template>

<script>
import { hideImg, LoadGallery, downloadImage, getImg, showImg, submitFile, deleteImage, handleFileUpload, mounted } from './http-api.js'
export default {
  name: "Images",
  props: {
    msg: String,
  },
  data() {
    return {
      response: [],
      errors: [],
      file: ''
    };
  },
  methods: {
    hideImg, 
    LoadGallery, 
    downloadImage, 
    getImg, 
    showImg,
    deleteImage,
    submitFile, 
    handleFileUpload
  },
  mounted
};


</script>

